import React, {Component} from 'react';

import Classes from './Conocenos.css';
import constructora2 from '../../assets/constructora2.jpg';
import descanso from '../../assets/descanso.jpg';
import enconstruccion from '../../assets/enconstruccion.gif';
import tractor from '../../assets/tractor.gif';



const extra = () => (
	<div className={Classes.info}>
	<br />
	<div className={Classes.contenttwo}>
	<center>
       <h1>Conoce de Nosotros</h1>
	</center>
			<div class={Classes.contentdetails}>


				<div className={Classes.contentdetailsfora}>
				<img src={tractor}/>
				<h3>HISTORIA</h3>
				<p>Dhamir Constructora e Inmobiliaria es una empresa dedicada a la construccin y gestin de proyectos inmobiliarios con ms de 13 aos a experiencia.
Kallpa Constructora e Inmobiliaria nace en el ao 2003, como una empresa dedicada a la construccin y gestin de proyectos arquitectnicos comerciales y de habitacin.</p>
				</div>
				<div className={Classes.contentdetailsfora}>
				<img src={enconstruccion}/>
				<h3>QUE OFRECEMOS?</h3>
				<p>Dhamir Constructora e Inmobiliaria ofrece a sus clientes la experiencia adquirida en estos 13 aos de operaciones en el mercado inmobiliario peruano, contamos con un equipo de trabajo profesional que atendern todas las solicitudes y requerimientos que nos presentan.</p>
				</div>

				<div className={Classes.contentdetailsfora}>
				<img src={descanso}/>
				<h3>EQUIPO</h3>
				<p>EN DHAMIR CONSTRUCTORA E INMOBILIARIA TENEMOS LAS MS ALTAS EXIGENCIAS DE CALIDAD EN LA ATENCIN PARA CON NUESTROS CLIENTES, CONTAMOS CON UN EQUIPO DE MS DE 15 PERSONAS DEDICADO A LA ASESORA EN LA COMPRA DE DEPARTAMENTOS Y VENTA DE LOS PROYECTOS DE CONSTRUCCIN E INVERSIN QUE MANEJAMOS.</p>
				</div>
				
				
			</div>
		</div>	
	</div>

)

export default extra;


